.page--news {
  background-color: var(--bg);
  min-height: 100vh;
}

.page-wrapper {
  max-width: 1440px;
  margin: 0 auto;
  position: relative;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –Ω–æ–≤–æ—Å—Ç–µ–π - –ö–ê–ö –£ –§–û–¢–û–ë–ê–ù–ö–ê */
.news-hero {
  padding: 70px 0 40px;
}

.news-container {
  padding: 0 16px;
  box-sizing: border-box;
  width: min(1180px, 92vw);
  margin: 0 auto;
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ –Ω–æ–≤–æ—Å—Ç–µ–π */
.news-hero-title-group {
  margin-bottom: 30px;
}

.news-hero-title {
  font-family: 'Kankin', serif;
  font-weight: 400;
  font-size: 143px;
  color: #7a745d;
  line-height: 1.1;
  margin: 0 0 10px 0;
  text-align: left;
}

.news-hero-subtitle {
  font-family: 'Georgia', serif;
  font-weight: 700;
  font-size: 34px;
  color: #7a745d;
  margin: 0;
  line-height: 1.1;
  max-width: 1203px;
  text-align: left;
}

/* –ë–∞–Ω–Ω–µ—Ä - –≤—ã—Ä–æ–≤–Ω—è—Ç—å –ø–æ –ª–µ–≤–æ–º—É –∫—Ä–∞—é –∫–∞–∫ –∑–∞–≥–æ–ª–æ–≤–æ–∫ */
.news-hero-banner {
  background-color: #767257; /* –ü—Ä—è–º–æ–π —Ü–≤–µ—Ç */
  border-radius: 10px;
  padding: 18px 24px;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  min-height: 60px;
}

.news-hero-banner p {
  color: #ffffff;
  font-family: 'Kankin', serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.2;
  margin: 0;
  text-align: left;
  width: 100%;
}

/* –ö–∞—Ä—Ç–æ—á–∫–∏ —Å—Ç–∞—Ç–µ–π - –ê–î–ê–ü–¢–ò–í–ù–ê–Ø –í–´–°–û–¢–ê —Å –±–æ–ª–µ–µ —Å–ø–µ—Ü–∏—Ñ–∏—á–Ω—ã–º–∏ —Å–µ–ª–µ–∫—Ç–æ—Ä–∞–º–∏ */
.news-article-card {
  display: flex;
  gap: 20px;
  align-items: stretch;
  border: 1px solid #000000;
  border-radius: 10px;
  overflow: hidden;
  background-color: transparent;
  margin-bottom: 60px;
  width: 100%;
}

.news-article-card:last-of-type {
  margin-bottom: 40px;
}

.news-article-card__image-wrapper {
  flex: 1 1 50%;
  display: flex;
  align-items: stretch;
  min-height: 300px;
}

.news-article-card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  flex-grow: 1;
}

.news-article-card__content {
  flex: 1 1 50%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  min-height: 300px;
}

.news-article-card__title {
  font-family: 'Kankin', serif;
  font-weight: 400;
  font-size: 80px;
  line-height: 86px;
  margin: 0 0 15px 0;
}

.news-article-card__text {
  font-family: 'Kankin', serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  flex-grow: 1;
  overflow: hidden;
}

/* –¢–æ–ª—å–∫–æ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –¥–ª—è –≤—Ç–æ—Ä–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏ */
.news-article-card--reverse {
  flex-direction: row-reverse;
}

/* –û—Å–Ω–æ–≤–Ω–æ–π –∫–æ–Ω—Ç–µ–Ω—Ç —Å—Ç—Ä–∞–Ω–∏—Ü—ã */
.news-content {
  padding-bottom: 60px;
}

.news-page-footer-text {
  font-family: 'Kankin', serif;
  font-size: 40px;
  line-height: 43px;
  text-align: center;
  margin: 10px 0 -50px 0;
  color: var(--text-dark);
  width: 100%;
}
/* –ê–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç—å */
@media (max-width: 1100px) {
  .news-article-card,
  .news-article-card--reverse {
    flex-direction: column;
    min-height: auto;
  }
  
  .news-article-card__image-wrapper {
    min-height: 250px;
  }
  
  .news-article-card__content {
    min-height: auto;
  }
  
  .news-article-card__title {
    font-size: 60px;
    line-height: 1.1;
  }
  
  .news-article-card__image {
    height: 250px;
  }
}

@media (max-width: 992px) {
  .news-hero-title {
    font-size: 100px;
  }
  
  .news-hero-subtitle {
    font-size: 28px;
  }
  
  .news-hero-banner p {
    font-size: 24px;
  }
}

@media (max-width: 768px) {
  .news-hero {
    padding: 50px 0 30px;
  }
  
  .news-container {
    padding: 0 12px;
  }
  
  .news-hero-title {
    font-size: 70px;
  }
  
  .news-hero-subtitle {
    font-size: 24px;
  }
  
  .news-hero-banner {
    padding: 12px 16px;
    margin-bottom: 30px;
    min-height: 50px;
    white-space: normal;
  }
  
  .news-hero-banner p {
    font-size: 18px;
    white-space: normal;
    line-height: 1.2;
  }
  
  .news-article-card__image-wrapper {
    min-height: 200px;
  }
  
  .news-article-card__title {
    font-size: 40px;
  }
  
  .news-article-card__text {
    font-size: 18px;
  }
  
  .news-article-card__image {
    height: 200px;
  }
  
  .news-article-card {
    margin-bottom: 30px;
  }
  
  .news-article-card:last-of-type {
    margin-bottom: 30px;
  }
  
  .news-content {
    padding-bottom: 40px;
  }
  
  .news-page-footer-text {
    font-size: 30px;
    line-height: 1.2;
    margin: 20px 0 0 0;
  }
}

/* –û–±—â–∏–µ —É—Ç–∏–ª–∏—Ç—ã */
.font-kankin {
  font-family: 'Kankin', serif;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.news-link {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-size: 20px;
  color: var(--text-dark);
  text-decoration: none;
  transition: color 0.3s ease, transform 0.2s ease;
}

.news-link:hover {
  color: var(--rust); /* –º–µ–Ω—è–µ–º —Ü–≤–µ—Ç –Ω–∞ –∞–∫—Ü–µ–Ω—Ç–Ω—ã–π */
  transform: translateX(4px); /* –Ω–µ–±–æ–ª—å—à–æ–π —Å–¥–≤–∏–≥ –≤–ø—Ä–∞–≤–æ –ø—Ä–∏ hover */
}

.news-link img {
  transition: transform 0.3s ease;
}

.news-link:hover img {
  transform: translateX(4px); /* —Å—Ç—Ä–µ–ª–∫–∞ —Ç–æ–∂–µ —Å–¥–≤–∏–≥–∞–µ—Ç—Å—è */
}
